import React from 'react';

const Welcome = () => (
  <div className="homepage">
    <h2 className="text-center forced-value">Welcome to League-Watcher Site!</h2>
    <h2 className="text-center forced-value">Here you find out about champions, spells, and items currently on League of Legends!</h2>
    <h2 className="text-center forced-value">Search for your favorite Champions and learn more about them!</h2>
    <h2 className="text-center forced-value">Click on the Links above to find out more!</h2>
  </div>
);

export default Welcome;



